.RegWindow{
    color: #FFFFFF8F;
    display: flex;
    flex-direction: row;
    padding: 2rem;
    background-color: #484848;
    font-family: 'Inter', sans-serif;
}

form{
    display: flex;
    flex-direction: column;
}

.RegWindow input{
    font-family: 'Inter', sans-serif;
    background: none;
    border: 0;
    margin-bottom: 2rem;
    border-bottom: 1px solid #FFFFFF8F;
    padding: 0.25rem;
    font-size: 1.1rem;
    font-weight: 400;
    line-height: 1.3rem;
    letter-spacing: 0em;
    text-align: left;
    color: #FFFFFF8F;
    color: white;
}

.RegWindow select{
    font-family: 'Inter', sans-serif;
    background: none;
    border: 0;
    margin-bottom: 2rem;
    border-bottom: 1px solid #FFFFFF8F;
    padding: 0.25rem;
    font-size: 1.1rem;
    font-weight: 400;
    line-height: 1.3rem;
    letter-spacing: 0em;
    text-align: left;
    color: #FFFFFF8F;
}

.RegWindow option{
    color: #484848;
    background: whitesmoke;
}
